import { pokemonData } from "./pokemon";
import { Table as TableAnt, Typography, Image} from 'antd';

//   
const { Text } = Typography;

//     How To Use,  ,    ,   /   
const columns = [
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    //  ,                 <Text>
     render: (text) => <Text>{text}</Text>,
    },
    {
      title: 'Numder',
      dataIndex: 'number',
      key: 'number',
    },
    {
      title: 'Class',
      dataIndex: 'classification',
      key: 'classification',
    //      ,    
      filters: [
        {
            text: 'Seed Pokmon',
            value: 'Seed Pokmon',
        },
        {
            text: 'Worm Pokmon',
            value: 'Worm Pokmon',
        },
        {
            text: 'Cocoon Pokmon',
            value: 'Cocoon Pokmon',
        },
      ],
    //  value - ,  ,       
      onFilter: (value, item) => item.classification.includes(value)
    },

    {
        title: 'Maximum HP',
        dataIndex: 'maxHP',
        key: 'maxHP',
        // ,    js (         )
        sorter: (a, b) => a.maxHP - b.maxHP,
      },
      {
        title: 'Maximum CP',
        dataIndex: 'maxCP',
        key: 'maxCP',
      },
      {
        title: 'Image',
        dataIndex: 'image',
        key: 'image',
        //   src ,    image     src  
        render: (src) => <Image src={src} width={150}/>
      },
  ];

//     
const dataPokemon = pokemonData.map(pokemon => ({
    name: pokemon.name,
    number: pokemon.number,
    classification: pokemon.classification,
    maxHP: pokemon.maxHP,
    maxCP: pokemon.maxCP,
    key: pokemon.id,
    image: pokemon.image,
}))

console.log(dataPokemon);

//   ,           /  ,      (= 10),    .  
const Table = ({rows = 10}) => {
    
    return (
        //        How To Use/pagination -  ,     - rows .  (    - ,     =      page)
        <TableAnt 
        dataSource={dataPokemon} 
        columns={columns} 
        pagination={{
            pageSize: rows,
            // pageSizeOptions: [20, 50, 100],
            //  page -  -    
            // showSizeChanger: false,
            //   100    
            // defaultPageSize: 100,

        }}/>
    )
}

export default Table;